import {observer} from 'mobx-react-lite'
import './Home.css'
import svg from './../../assets/tests.svg'
import React, { Component }  from 'react';

const Home = observer(() => {
    return(
        <div className="home">
            <div className="top-wrapper">
                <h5 className="welcome-text">
                    Сделаем ваш лабораторный практикум за вас
                </h5>
            </div>
            <div className="mid-wrapper">
                <img className="tests-img" alt="" src={svg}/>
                <div className="mid-wrapper-typo">
                    <p className="mid-wrapper-title">Уникальная и удобная система</p>
                    <p className="mid-wrapper-text">
                        Дипломные и другие виды работ на заказ. Биржа студенческих работ к вашим услугам.
                        Выставляйте задание и исполнитель сам найдет вас!
                    </p>
                    <p className="mid-wrapper-title">Становитесь исполнителем</p>
                    <p className="mid-wrapper-text">
                        Выполняйте задание и получайте за них деньги. Быстрые выплаты на банковские карты.
                    </p>
                    <p className="mid-wrapper-title">Еффективная система отзывов</p>
                    <p className="mid-wrapper-text">
                        Еффективная система отзывов не даст злоумышленнику обмануть вас. Честно оценивайте исполнителей,
                        получайте за это бонусы.
                    </p>
                </div>
            </div>
        </div>
    )
})

export default Home