import './Signin.css'
import {observer} from 'mobx-react-lite'
import {Formik} from "formik";
import {Button, Input} from "antd";
import * as yup from "yup";
import authService from '../../services/authService'
import {useHistory} from "react-router-dom";
import React, { Component }  from 'react';


const Signin = observer(() => {

    const history = useHistory()

    const validator = yup.object().shape({
        email: yup.string().email().required('Required'),
        password: yup.string().required('Required')
    })

    const loginHandler = async (data) => {
        await authService.login(data, history)
    }

    return(
        <div className="signin">
            <Formik initialValues={{
                email: '',
                password: ''
            }} validateOnBlur validationSchema={validator} onSubmit={async (values) => await loginHandler(values)}
            >
                {({
                      values,
                      errors,
                      touched,
                      handleChange,
                      handleBlur,
                      isValid,
                      handleSubmit,
                      dirty
                  }) => (
                    <div className="signin-form">
                        <h3>Login form</h3>
                        {touched.email && errors.email && <p className="dynamic-error">{errors.email}</p>}
                        <p>
                            <Input
                                placeholder="Email"
                                type="text"
                                name="email"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.email}
                            />
                        </p>
                        {touched.password && errors.password && <p className="dynamic-error">{errors.password}</p>}
                        <p>
                            <Input
                                placeholder="Password"
                                type="password"
                                name="password"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.password}
                            />
                        </p>
                        <Button type="primary" disabled={!isValid && !dirty} onClick={handleSubmit} type="submit">Login</Button>
                    </div>
                )}
            </Formik>
        </div>
    )
})

export default Signin