import {observer} from 'mobx-react-lite'
import {Formik} from 'formik'
import './Signup.css'
import * as yup from 'yup'
import {Button, Input} from 'antd'
import authService from '../../services/authService'
import {useHistory} from "react-router-dom";
import React, { Component }  from 'react';

const Signup = observer(() => {

    const history = useHistory()

    const validator = yup.object().shape({
        email: yup.string().email().required('Required'),
        password: yup.string().required('Required'),
        confirmPassword: yup.string().oneOf([yup.ref('password')],'Password mismatch').required('Required')
    })

    const registerHandler = async (data) => {
        await authService.register(data, history)
    }

    return(
        <div className="signup">
            <Formik initialValues={{
                email: '',
                password: '',
                confirmPassword: ''
            }} validateOnBlur validationSchema={validator} onSubmit={async (values) => await registerHandler(values)}
            >
                {({
                      values,
                      errors,
                      touched,
                      handleChange,
                      handleBlur,
                      isValid,
                      handleSubmit,
                      dirty
                }) => (
                   <div className="signup-form">
                       <h3>Registration form</h3>
                       {touched.email && errors.email && <p className="dynamic-error">{errors.email}</p>}
                       <p>
                           <Input
                               placeholder="Email"
                               type="text"
                               name="email"
                               onChange={handleChange}
                               onBlur={handleBlur}
                               value={values.email}
                           />
                       </p>
                       {touched.password && errors.password && <p className="dynamic-error">{errors.password}</p>}
                       <p>
                           <Input
                               placeholder="Password"
                               type="password"
                               name="password"
                               onChange={handleChange}
                               onBlur={handleBlur}
                               value={values.password}
                           />
                       </p>
                       {touched.confirmPassword && errors.confirmPassword && <p className="dynamic-error">{errors.confirmPassword}</p>}
                       <p>
                           <Input
                               placeholder="Repeat password"
                               type="password"
                               name="confirmPassword"
                               onChange={handleChange}
                               onBlur={handleBlur}
                               value={values.confirmPassword}
                           />
                       </p>

                       <Button type="primary" disabled={!isValid && !dirty} onClick={handleSubmit} type="submit">Register</Button>
                   </div>
                )}
            </Formik>
        </div>
    )
})

export default Signup