import { observer } from 'mobx-react-lite'
import authStore from './../../store/authStore'
import {Link, useHistory} from 'react-router-dom'
import './Nav.css'
import React, { Component }  from 'react';

const Nav = observer(() => {
    const history = useHistory()

    const logoutHandler = () => {
        authStore.logout()
    }

    return(
        <div className="nav">
            <span className="spacer"/>
            <p onClick={() => history.push('/')} className="nav-title">StudentHelp <span className="freelance-span">!freelance</span></p>
            <span className="spacer"/>
            {authStore.isLoggedIn ? <Link to="/deals" className="link">Deals</Link> : <Link to="/signin" className="link">Sign-in</Link>}
            {authStore.isLoggedIn ? <p className="link" onClick={logoutHandler}>Logout</p> : <Link to="/signup" className="link">Sign-up</Link>}
        </div>
    )
})

export default Nav