import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Nav from './components/Nav/Nav'
import Home from './containers/Home/Home'
import Signup from './containers/Signup/Signup'
import Signin from './containers/Signin/Signin'
import {ClipLoader} from 'react-spinners'
import apiStore from './store/apiStore'
import authStore from './store/authStore'
import './App.css'
import {observer} from 'mobx-react-lite'
import Deals from './containers/Deals/Deals'
import React, { Component }  from 'react';

const App = observer(() => {
    return(
        <div className="app">
            <div className="spinner">
                <ClipLoader color="maroon" loading={apiStore.isFetching} size={150}/>
            </div>
            <Router>
                <Nav />
                <Switch>
                    <Route exact path="/" component={Home}/>
                    <Route exact path="/deals" component={Deals}/>
                    {!authStore.isLoggedIn && <Route exact path="/signup" component={Signup}/>}
                    {!authStore.isLoggedIn && <Route exact path="/signin" component={Signin}/>}
                </Switch>
            </Router>
        </div>
    )
})

export default App